._card:hover{
    box-shadow: 1px 1px 10px  rgba(0,0,0,.3) ,-.2px -.2px 10px rgba(0,0,0,.3);
    transform: translate(0px,5px); 
}
._anime:hover{
    animation: giro infinite 5s linear;
}
.java:hover{
    animation: java infinite 5s linear;
}
._card{
    transition:All 1s ease;
    box-sizing: none;
}
._anime{
    transition:All 1s ease;
}
li:hover{
    color:black;
    text-decoration:underline;
}
ol li{
    color:white;
    text-decoration: dashed;
    transition:All 1s ease;
}
@keyframes giro{
    100%{
       transform: rotate(720deg);
    }

}
@keyframes java{
    0%{
        transform: rotate(0deg);
    }
    10%{
        transform: rotate(-30deg);
    }
    20%{
        transform: rotate(0deg);
    }
    30%{
        transform: rotate(30deg);
    }
    40%{
        transform: rotate(0deg);
    }
    50%{
        transform: rotate(-30deg);
    }
    60%{
        transform: rotate(0deg);
    }
    70%{
        transform: rotate(30deg);
    }
    80%{
        transform: rotate(0deg);
    }
    90%{
        transform: rotate(-30deg);
    }
    100%{
        transform: rotate(0deg);
    }
    110%{
        transform: rotate(30deg);
    }
    
}