.rt{
    animation:rt normal 5s ease;
    transform: translate(0px)!important;
}
.ini{
    transform: translate(-1800px);
}
@keyframes rt{
    10%{transform: translate(-1800px);}
    20%{transform: translate(0px)rotate(360deg);}

    50%{transform: translate(0px)rotate(2deg)}
    60%{transform: translate(0px)rotate(-2deg)}
    70%{transform: translate(0px)rotate(1deg)}
    80%{transform: translate(0px)rotate(-1deg)}
    100%{transform: translate(0px)rotate(0deg);}
}